<template>
  <div class="withdrawal mescroll">
    <div class="mid">
      <p class="title">转出给好友,请输入好友手机号</p>
      <div class="flex">
        <p class="font">手机号</p>
        <input type="number" v-model="price" placeholder="请输入" />
      </div>
      <p class="text">
        账户余额：¥{{allPrice.toFixed(2)}}
        <!-- <span @click="all()">全部{{$route.query.type == '1'?'提现':'转出'}}</span> -->
      </p>
      <div class="btn" @click="submit">查 找 好 友</div>
      <div class="bottom">
        <p class="prompt">温馨提示</p>
        <p class="promptContent">
            佣金转移至好友的应付款后，对方的应付款只能用来支付，不可转出。
        </p> 
      </div>
    </div>
  </div>
</template>
<script>
import { Field, Dialog } from "vant";
import { getPersonAccount, withdrawal, toBalance,findFriends } from "@/api";
import { element } from "prop-types";
export default {
  components: {
    [Field.name]: Field,
    [Dialog.name]: Dialog
  },
  data() {
    return {
      price: "",
      allPrice: 0,
      clickFlag: true,
      userCode: ""
    };
  },
  methods: {
    all() {
      this.price = this.allPrice;
    },
    toJump(){
      this.$route.push({name: "paytoFriends"});
    },
    submit() {
      if (!this.clickFlag) {
        return;
      } 
      if (!this.price) {
        this.$toast("请输入好友手机号");
        return;
      } 
      this.clickFlag = false;
      console.log(111);
      let that = this;
        findFriends({
              usermobile: this.price 
            }).then(res => {
              //loading.clear();
              if (res.code == 0) { 
                that.$toast(res.msg);
                 this.$router.push({
                    name: "pay",
                    query: {
                        type: 2,
                        userCode: res.data.userCode
                    },
                });
                 //window.location.href = this.$config.host2 + "/wx/#/pay?type=2&userCode="  + res.data.userCode;
               
              } else {
                that.clickFlag = true;
                that.$toast(res.msg);
              }
            }).catch(() => {
          // on cancel
          that.clickFlag = true;
        });
      // } else {
      //   this.$toast.allowMultiple();
      //   const loading = this.$toast.loading({
      //     duration: 0, // 持续展示 toast
      //     forbidClick: true // 禁用背景点击
      //   });
      //   withdrawal({
      //     userCode: this.userCode,
      //     moneyNum: parseInt(this.price * 100)
      //   }).then(res => {
      //     loading.clear();
      //     if (res.code == 0) {
      //       this.$toast.success("提现成功");
      //       setTimeout(() => {
      //         this.$router.back();
      //       }, 1000);
      //     } else {
      //       this.clickFlag = true;
      //       this.$toast(res.msg);
      //     }
      //   });
      // }
    },
    loadData() {
      this.$toast.allowMultiple();
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      getPersonAccount().then(res => {
        loading.clear();
        if (res.code == 0) {
          this.allPrice = res.data.myAccount.acAmt;
          this.userCode = res.data.myAccount.userCode;
        } else {
          this.$toast.fail(res.msg);
        }
      });
    }
  },
  mounted() {
    this.loadData();
  }
};
</script>
<style lang="scss" scoped>
.withdrawal {
  background: #fff;
  .mid {
    padding: 0 15px;
    .title {
      padding: 10px 0;
      font-size: 15px;
      line-height: 25px;
      color: #999;
    }
    .flex {
      display: flex;
      border-bottom: 1pt solid #e1e1e1;
      padding: 5px 0 10px;
      .font {
        margin-right: 5px;
      }
      input {
        border: 0;
        width: 100%;
        height: 30px;
        font-size: 30px;
        line-height: 30px;
      }
    }
    .text {
      color: #666;
      line-height: 25px;
      font-size: 15px;
      margin: 10px 0;
      span {
        color: #eb5252;
        margin-left: 5px;
      }
    }
    .btn {
      background: #eb5252;
      color: #fff;
      margin: 20px 0 0;
      height: 50px;
      text-align: center;
      line-height: 50px;
      letter-spacing: 1px;
      border-radius: 5px;
    }
    .bottom {
      position: fixed;
      bottom: 20px;
      width: 100%;
      left: 0;
      text-align: center;
      line-height: 22px;
      color: #999;
      .prompt {
        font-weight: bold;
        font-size: 15px;
      }
      .promptContent {
        font-size: 13px;
      }
    }
  }
}
</style>
